import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from '../../environments/environment';
import { UserHelper } from '../helpers/user.helper';
import { stringify } from 'querystring';
import { JwtHelperService } from '@auth0/angular-jwt';
@Injectable()
export class SpaceService {
    authToken;
    url: string = environment.urlApi;
    userHelper: UserHelper = new UserHelper();
    helper: JwtHelperService = new JwtHelperService();
    constructor(private httpClient: HttpClient) { }

    // Upload method
    private toFormData(serverFieldName, name) {
        const form = new FormData();
        form.append('file', this.dataURItoBlob(serverFieldName), name);
        return form;
    }

    // Upload method
    public dataURItoBlob(dataURI) {
        const byteString = atob(dataURI.split(',')[1]);
        const ab = new ArrayBuffer(byteString.length);
        return new Blob([ab], { type: 'image/jpeg' });
    }

    async getById(params): Promise<any> {
        const createUrl = `${this.url}spaces/findbyid?${stringify(params)}`;
        return await this.httpClient.get<any>(createUrl, this.userHelper.createOptions()).toPromise();
    }

    async getAllPaginated(query): Promise<any> {
        if (this.getUserInfo()) {
            const userInfo = this.getUserInfo();
            query += '&user_id=' + userInfo.id;
        }
        const createUrl = `${this.url}spaces/?${query}`;
        return await this.httpClient.get<any>(createUrl).toPromise();
    }

    async postVisit(space_id: string): Promise<any> {
        const postObj = { space_id };
        const createUrl = `${this.url}spacevisit/savevisit`;
        return await this.httpClient.post<any>(createUrl, postObj).toPromise();
    }

    async getSimilar(category_id: number, space_id: number): Promise<any> {
        let createUrl = `${this.url}spacevisit/getSimilar/${category_id}?space_id=${space_id}`;
        if (this.getUserInfo()) {
            const userInfo = this.getUserInfo();
            createUrl += '&user_id=' + userInfo.id;
        }
        return await this.httpClient.get<any>(createUrl).toPromise();
    }

    async uploadImage(image: any, name: string): Promise<any> {
        const uploadUrl = `${this.url}spaces/upload`;
        const uploadedImage = await this.httpClient.post<any>(uploadUrl, this.toFormData(image, name)).toPromise();
        return uploadedImage;
    }

    async favoriteSpace(user_favorites): Promise<any> {
        const url = `${environment.urlApi}userfavorites`;
        return await this.httpClient.post(url, user_favorites).toPromise();
    }

    async deleteFavoriteSpace(user_favorites): Promise<any> {
        const query = stringify(user_favorites);
        const url = `${environment.urlApi}userfavorites?${query}`;
        return await this.httpClient.delete(url, user_favorites).toPromise();
    }

    getUserInfo() {
        if (localStorage.getItem('token-user')) {
            if (this.userHelper.verifyToken(localStorage.getItem('token-user'))) {
                const userInfo = this.helper.decodeToken(localStorage.getItem('token-user'));
                return userInfo;
            }
        }

        return undefined;
    }

}
