import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { UserModel } from '../../models/user.model';
import { UserService } from '../../services/user.service';
import '../../helpers/getFBSDK';
declare var FB: any;

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
  modalImage = false;
  @Output() clickout = new EventEmitter();
  @Output() loginModal = new EventEmitter();
  step = 1;
  userModel: UserModel = new UserModel();
  confirmation: string;
  email = '';
  emailError = '';
  errorSocial = '';
  errorSocialShow: boolean = false;
  constructor(
    private userService: UserService
  ) { }

  ngOnInit() {
  }

  registerFacebook() {
    FB.api('/me?fields=id,name,email,gender', (response) => {
      if (response.email) {
        this.userModel.facebook_id = response.id;
        this.userModel.name = response.name;
        this.userModel.email = response.email;  
        this.saveFacebook(this.userModel);
      }
    });    
  }

  handleLoginFacebook() {
    FB.getLoginStatus((response) => {
      if (response.status !== 'connected') {
        FB.login(function(response) {
          this.registerFacebook()
        }, {scope: 'public_profile,email'})
      } else {
        this.registerFacebook()        
      }
    });
  }

  close() {
    this.clickout.emit();
  }

  nextStep() {
    if (this.step < 3) {
      this.step++;
    }
  }

  lastStep() {
    if (this.step > 1) {
      this.step--;
    }
  }

  openModalImage() {
    this.modalImage = !this.modalImage;
  }

  setUserImage(image) {
    this.userModel.image = image.image;
  }

  async save(form) {
    if (form.valid) {
      try {
        const user = await this.userService.create(this.userModel);
        this.close();
      } catch (e) {
        console.log(e);
        if (e.error.friendly_message === 'Já existe um usuário com este email') {
          this.emailError = e.error.friendly_message;
        } else { this.emailError = '' }
      }
    }
  }
  async saveFacebook(item) {
    try {
      const user = await this.userService.create(item);
      this.errorSocialShow = true;
      this.close();
    } catch (e) {
      throw e
    }
    this.errorSocialShow = true;
  }

  emmitLoginModal(){
    this.loginModal.emit()
  }
}
