import { Component, OnInit, ViewChild, Output, EventEmitter, Input } from '@angular/core';
import { ImageCropperComponent, CropperSettings } from 'ng2-img-cropper';
import { UserService } from '../../../services/user.service';

@Component({
  selector: 'app-image-user-modal',
  templateUrl: './image-user-modal.component.html',
  styleUrls: ['./image-user-modal.component.css'],
})
export class ImageUserModalComponent implements OnInit {
  @Output() close = new EventEmitter();
  @Output() imageUploaded = new EventEmitter();
  files;
  showingWebCam = false;
  data: any;
  cropperSettings: CropperSettings;
  edited = false;
  croppedWidth: number;
  croppedHeight: number;
  picture: any;
  name = '';
  @ViewChild('cropper') cropper: ImageCropperComponent;
  public dropped(event: any) {
    this.files = event.files;
    for (const file of event.files) {
      file.fileEntry.file(info => {
        const image: any = new Image();
        const fileInfo: File = info;
        const myReader: FileReader = new FileReader();
        const that = this;
        myReader.onloadend = function (loadEvent: any) {
          image.src = loadEvent.target.result;
          that.cropper.setImage(image);

          that.picture = image;

        };
        myReader.readAsDataURL(fileInfo);
      });
    }
  }

  constructor(private userService: UserService) {
    this.cropperSettings = new CropperSettings();
    this.cropperSettings.dynamicSizing = true;
    this.cropperSettings.croppedWidth = 900;
    this.cropperSettings.croppedHeight = 900;

    this.cropperSettings.canvasWidth = 900;
    this.cropperSettings.canvasHeight = 900;

    this.cropperSettings.cropperDrawSettings.strokeColor = '#0091ea';
    this.cropperSettings.cropperDrawSettings.strokeWidth = 2;
    this.cropperSettings.fileType = 'image/jpeg';
    this.cropperSettings.cropperClass = 'croperXD';
    this.cropperSettings.noFileInput = true;
    this.data = {};
  }

  ngOnInit() {
  }

  public fileOver(event) {
    console.log(event);
  }

  public fileLeave(event) {
    console.log(event);
  }

  fileChangeListener($event) {
    this.edited = true;
    this.showingWebCam = false;
    const image: any = new Image();
    const file: File = $event.target.files[0];
    const myReader: FileReader = new FileReader();
    const that = this;
    this.name = $event.target.files[0].name;
    myReader.onloadend = function (loadEvent: any) {
      image.src = loadEvent.target.result;
      that.cropper.setImage(image);
      that.picture = image;

    };
    myReader.readAsDataURL(file);
  }

  closeModalImage() {
    this.close.emit();
  }

  async upload() {
    if (this.data['image']) {
      try {
        const url = await this.userService.uploadImage(this.data['image'], this.name);
        this.imageUploaded.emit({ image: url });
        this.close.emit();
      } catch (e) {
        console.log(e);
      }
    } else {
      return;
    }
  }
}
