export class UserModel {
    id: number;
    name: string;
    lastname: string;
    has_space: boolean;
    facebook_id: string;
    password:string;
    email: string;
    linkedin_id: string;
    google_id: string;
    created_at: Date;
    updated_at: Date;
    gender: string;
    telephone: string;
    birth_date: Date;
    cellphone: string;
    image: string;

    constructor() {
        this.name = '';
        this.lastname = '';
        this.facebook_id = '';
        this.password = '';
        this.email = '';
        this.linkedin_id = '';
        this.google_id = '';
        this.gender = '';
        this.telephone = '';
        this.cellphone = '';
        this.image = '';
    }

    loadModel(response: any) {
        this.id = response.id;
        this.name = response.name;
        this.has_space = response.has_space;
        this.facebook_id = response.facebook_id;
        this.email = response.email;
        this.linkedin_id = response.linkedin_id;
        this.google_id = response.google_id;
        this.created_at = response.created_at;
        this.updated_at = response.updated_at;
        this.gender = response.gender;
        this.telephone = response.telephone;
        this.birth_date = response.birth_date;
        this.cellphone = response.cellphone;
        this.image = response.image;
    }
}
