import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { LoginModel } from '../../models/login.model';
import { UserService } from '../../services/user.service';
import '../../helpers/getFBSDK';
declare var FB: any;

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})

export class LoginComponent implements OnInit {
  @Output() clickout = new EventEmitter();
  loginModel: LoginModel = new LoginModel();
  constructor(private userService: UserService) { }

  ngOnInit() {
  }

  close() {
    this.clickout.emit();
  }

  test() {
    
  }

  handleLoginFacebook() {
    FB.getLoginStatus(function(response) {
      if (response.status !== 'connected') {
        FB.login(function(response) {
          console.log(response);
        }, {scope: 'public_profile,email'})
      } else {
        console.log('Welcome!  Fetching your information.... ');
        FB.api('/me?fields=id,name,email,gender', function(response) {
          console.log(response);
          console.log(response.email);
          console.log(response.name);
        });
      }
    });
  }
  

  async login() {
    try {
      const info = await this.userService.login(this.loginModel);
      localStorage.setItem('user-info', JSON.stringify({ name: info.name, lastname: info.lastname, email: info.email }));
      localStorage.setItem('token-user', info.token);
      this.clickout.emit();
    } catch (e) {
      console.log(e);
    }
  }

}
