import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { AppComponent } from './app.component';
import { AppRoutes } from './app.routing';
import { RouterModule } from '@angular/router';
import { HeaderComponent } from './layout/header/header.component';
import { FooterComponent } from './layout/footer/footer.component';
import { ClientComponent } from './layout/client/client.component';
import { RegisterComponent } from './pages/register/register.component';
import { ImageUserModalComponent } from './pages/register/image-user-modal/image-user-modal.component';
import { ImageCropperModule } from 'ng2-img-cropper';
import { UserService } from './services/user.service';
import { HttpClientModule } from '@angular/common/http';
import { FormsModule } from '@angular/forms';
import { AppMaskDirective } from './directives/mask.directive';
import { SweetAlert2Module } from '@toverux/ngx-sweetalert2';
import { CategoryService } from './services/category.service';
import { SpaceService } from './services/space.service';
import { MostWantedService } from './services/mostwanted.service';
import { SharedModule } from './shared/shared.module';
import { SpaceTagsService } from './services/space_tags.service';
import { CityService } from './services/city.service';

@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    FooterComponent,
    ClientComponent,
    RegisterComponent,
    ImageUserModalComponent,
    AppMaskDirective
  ],
  imports: [
    BrowserModule,
    RouterModule.forRoot(AppRoutes, { useHash: true }),
    HttpClientModule,
    FormsModule,
    SweetAlert2Module.forRoot(),
    ImageCropperModule,
    SharedModule
  ],
  exports: [
    AppMaskDirective,
    ImageCropperModule
  ],
  providers: [UserService, CategoryService, SpaceService, MostWantedService, SpaceTagsService, CityService],
  bootstrap: [AppComponent]
})
export class AppModule { }
