import { Injectable } from '@angular/core';

@Injectable()
export class CityService {
    constructor() { }
    city: any = [
        {
            id: 1,
            name: 'São Paulo'
        },
        {
            id: 2,
            name: 'Rio de Janeiro'
        }
    ];

    async getAllCities(): Promise<any> {
        return this.city;
    }
}