import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from '../../environments/environment';
import { LoginModel } from '../models/login.model';
import { UserModel } from '../models/user.model';

@Injectable()
export class UserService {
    authToken;
    url: string = environment.urlApi;
    constructor(private httpClient: HttpClient) { }

    // Upload method
    private toFormData(serverFieldName, name) {
        var form = new FormData();
        console.log(this.dataURItoBlob(serverFieldName))
        form.append("file", this.dataURItoBlob(serverFieldName), name);
        return form
    }

    // Upload method
    public dataURItoBlob(dataURI) {
        var byteString = atob(dataURI.split(',')[1]);
        var ab = new ArrayBuffer(byteString.length);
        var ia = new Uint8Array(ab);
        for (var i = 0; i < byteString.length; i++) {
            ia[i] = byteString.charCodeAt(i);
        }
        return new Blob([ab], { type: 'image/jpeg' });
    }

    async login(login: LoginModel): Promise<any> {
        let loginUrl = `${this.url}users/auth`
        return await this.httpClient.post<LoginModel>(loginUrl, login).toPromise();
    }

    async validateEmail(email: string): Promise<any> {
        let validateUrl = `${this.url}users/validate-email`;
        return await this.httpClient.post<any>(validateUrl, email).toPromise();
    }

    async create(user: UserModel): Promise<any> {
        try {
            let createUrl = `${this.url}users`;
            return await this.httpClient.post<any>(createUrl, user).toPromise();
        } catch (e) {
            throw e
        }
        
    }

    async uploadImage(image: any, name: string): Promise<any> {
        let uploadUrl = `${this.url}users/upload`;
        let uploadedImage = await this.httpClient.post<any>(uploadUrl, this.toFormData(image, name)).toPromise();
        return uploadedImage;
    }

}
